}
.slider {
    width: 100%;
    height: 90vh;
    position: relative;
    overflow: hidden;
  }
  
  .slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    transition: all 0.5s ease;
  }
  
  @media screen and (min-width: 600px) {
    .slide img {
      width: 100%;
      height: 100%;
    }
  }
  
  .slide img {
    width: 100%;
    height: 100%;
  }
  
  .current {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  
  .content {
    position: absolute;
    top: 23rem;
    left: 5rem;
    opacity: 0;
    width: 50%;
    color: #fff;
    padding: 3rem;
    background: rgba(0, 0, 0, 0.3);
    -webkit-animation: slide-up 1s ease 0.5s;
            animation: slide-up 1s ease 0.5s;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    visibility: hidden;
  }
  
  @-webkit-keyframes slide-up {
    0% {
      visibility: visible;
      top: 23rem;
    }
    100% {
      visibility: visible;
      top: 17rem;
    }
  }
  
  @keyframes slide-up {
    0% {
      visibility: visible;
      top: 23rem;
    }
    100% {
      visibility: visible;
      top: 17rem;
    }
  }
  
  @media screen and (max-width: 600px) {
    .content {
      width: 80%;
    }
  }
  
  .content > * {
    color: #fff;
    margin-bottom: 1rem;
  }
  
  .current .content {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
    transition: all 0.5s ease;
  }
  
  .arrow {
    border: 2px solid #F58220;
    background-color: transparent;
    color: #F58220;
    cursor: pointer;
    height: 2rem;
    width: 2rem;
    border-radius: 50%;
    position: absolute;
    z-index: 999;
  }
  .arrow:hover {
    background-color: #F58220;
    color: #fff;
  }
  
  .next {
    top: 50%;
    right: 1.5rem;
  }
  .prev {
    top: 50%;
    left: 1.5rem;
  }
  
  hr {
    height: 2px;
    background: white;
    width: 50%;
  }
  
